.wrapper{
    padding: 50px;
    background: #003661;
    display: flex;
    justify-content: center;
    position: relative;
    height: 100vh;
    align-items: center;
    overflow: hidden;
}
.bgimage{
    background-size: cover; 
    position: absolute; 
    top: 0; 
    left: 0; 
    right: 0; 
    bottom: 0; 
    opacity: 0.2;
}
.bgvideo {
    position: absolute;
    top: 50%;
    left: 50%;
    opacity: 0.2;
    height: 100%;
    width: 177.77777778vh;/* 100 * 16 / 9 */
    min-width: 100%;
    min-height: 56.25vw;/* 100 * 9 / 16 */
    transform: translate(-50%, -50%);
}
.content{
    z-index: 100;
    flex: 1 1 0px;
    display: flex;
    justify-content: center;
    max-width: 800px;
}
.textWrapper{
    width: 100%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 470px;
}
.caption{
    margin: 0;
    font-size: 24px;
    max-width: 340px;
    color: white;
    font-weight: 200;
}
.button{
    display: block;
    cursor: pointer;
    width: 300px;
    max-width: 100%;
    padding: 0 0.5em;
    height: 50px;
    background-color: #2592ff;
    color: white;
    margin-top: 30px;
    font-weight:700;
    border-radius: 10px;
    line-height: 50px;
    text-decoration: none;
}

@media (max-width: 899px) {
    .circuit{
        display: none;
    }
}
@media (max-width: 569px) {
    .caption{
        margin-top: 30px;
    }
    .wrapper{
        padding: 20px;
    }
}